/* eslint-disable react/prop-types */
import React from 'react'
import { connect } from 'react-redux'
import { selectFormularioVeiculo } from '../../../redux/formulario/formulario.selector'
import { createStructuredSelector } from 'reselect'
import { Row, Col, Form, Input, Select, Radio, DatePicker, Divider } from 'antd'
import { handleChange } from '../../../redux/formulario/formulario.actions'
import { trigger } from '../../../redux/formulario/formulario.utils'
import carro from '../../../assets/icones/carro.png'
import moto from '../../../assets/icones/moto.png'
import caminhonete from '../../../assets/icones/caminhonete.png'
import vuc from '../../../assets/icones/vuc.png'
import { RadioVeiculo, DateConfig, ErroPersonalizado } from '../..'
import moment from 'moment'

import './style.css'

const { Option } = Select

/**
 * Componente do Formulário, parte 2.
 * Nessa seção é retirada todas as informações do veículo do usuário.
 * Todos os campos são obrigatórios.
 */
// eslint-disable-next-line react/prop-types
const Veiculo = ({ veiculo, handleChange }) => {
  return (
    <>
      <Row gutter={32}>
        <Col md={12}>
          <Form.Item
            label={<label className="labelFormVeiculo" htmlFor='input-marcaPneu'>MARCA DO PNEU</label>}
            required
            hasFeedback
            validateStatus={veiculo.errors ? veiculo.errors.marca ? 'error' : 'success' : null }
            extra={ veiculo.errors ? veiculo.errors.marca ? <ErroPersonalizado msg={veiculo.errors.marca} /> : null : null }
          >
            <Input
              value={veiculo.marca}
              id='input-marcaPneu'
              style={{ fontFamily: "'Open Sans', sans-serif" }}
              onChange={e => handleChange(e.target.value, trigger.MARCA_PNEU)}
              placeholder='Bridgestone, Pirelli, Goodyear.'
            />
          </Form.Item>
        </Col>
        <Col md={12}>
          <Form.Item
            label={<label className="labelFormVeiculo" htmlFor='input-fabricacao'>FABRICAÇÃO</label>}
            required
            hasFeedback
            validateStatus={veiculo.errors ? veiculo.errors.fabricacao ? 'error' : 'success' : null }
            extra={ veiculo.errors ? veiculo.errors.fabricacao ? <ErroPersonalizado msg={veiculo.errors.fabricacao} /> : null : null }
          >
            <Select
              style={{ width: '100%', fontFamily: "'Open Sans', sans-serif" }}
              id='input-fabricacao'
              placeholder='Selecione a opção'
              onChange={(e) => handleChange(e, trigger.TIPO_FABRICACAO)}
              value={veiculo.fabricacao}
            >
              <Option value='Nacional'>Nacional</Option>
              <Option value='Internacional'>Internacional</Option>
            </Select>
          </Form.Item>
        </Col>
        <Col md={24}>
          <Form.Item
            label={<label className="labelFormVeiculo" htmlFor='input-tipoDeVeiculo'>TIPO DE VEÍCULO</label>}
            required
            extra={ veiculo.errors ? veiculo.errors.tipo_de_veiculo ? <ErroPersonalizado msg={veiculo.errors.tipo_de_veiculo} /> : null : null }
          >
            <Radio.Group
              onChange={(e) => handleChange(e.target.value, trigger.TIPO_DE_VEICULO)}
              value={veiculo.tipo_de_veiculo}
              id='input-tipoDeVeiculo'
              className='tipoVeiculoRadioGroup'
            >
              <Row gutter={[32, 32]}>
                <Col xs={12} lg={6}>
                  <RadioVeiculo value='car' icone={carro} alt='Carro' label='CARRO' checked={veiculo.tipo_de_veiculo === 'car'} />
                </Col>
                <Col xs={12} lg={6}>
                  <RadioVeiculo value='moto' icone={moto} alt='Moto' label='MOTO' checked={veiculo.tipo_de_veiculo === 'moto'} />
                </Col>
                <Col xs={12} lg={6}>
                  <RadioVeiculo value='caminhonete' icone={caminhonete} alt='caminhonete' label='CAMINHONETE' checked={veiculo.tipo_de_veiculo === 'caminhonete'} />
                </Col>
                <Col xs={12} lg={6}>
                  <RadioVeiculo value='vuc' icone={vuc} alt='V.U.C' label='V.U.C' checked={veiculo.tipo_de_veiculo === 'vuc'} />
                </Col>
              </Row>
            </Radio.Group>
          </Form.Item>
        </Col>
        <Col md={12}>
          <Form.Item
            label={<label className="labelFormVeiculo" htmlFor='input-valorTotal'>VALOR TOTAL DOS PNEUS</label>}
            required
            hasFeedback
            validateStatus={ veiculo.errors ? veiculo.errors.total ? 'error' : 'success' : null }
            extra={ veiculo.errors ? veiculo.errors.total ? <ErroPersonalizado msg={veiculo.errors.total} /> : null : null }
          >
            <Input
              value={veiculo.total}
              id='input-valorTotal'
              style={{ fontFamily: "'Open Sans', sans-serif" }}
              onChange={e => {
                let valor = e.target.value
                valor = valor.replace(/\D/g, '')
                handleChange(valor, trigger.VALOR_TOTAL_PNEU)
              }}
              placeholder='Apenas números inteiros'
            />
          </Form.Item>
        </Col>
        <Col md={12}>
          <Form.Item
            label={<label className="labelFormVeiculo" htmlFor='input-quantidadePneus'>QUANTIDADE DE PNEUS</label>}
            required
            hasFeedback
            validateStatus={veiculo.errors ? veiculo.errors.qtd ? 'error' : 'success' : null }
            extra={ veiculo.errors ? veiculo.errors.qtd ? <ErroPersonalizado msg={veiculo.errors.qtd} /> : null : null }
          >
            <Input
              value={veiculo.qtd}
              id='input-quantidadePneus'
              style={{ fontFamily: "'Open Sans', sans-serif" }}
              onChange={e => {
                let valor = e.target.value
                valor = valor.replace(/\D/g, '')
                handleChange(valor, trigger.QUANTIDADE_PNEU)
              }}
              placeholder='Apenas números inteiros'
            />
          </Form.Item>
        </Col>
        <Col md={24}>
          <Form.Item
            label={<label className="labelFormVeiculo" htmlFor='input-placaVeiculo'>PLACA DO SEU VEÍCULO</label>}
            required
            hasFeedback
            validateStatus={veiculo.errors ? veiculo.errors.vehiclePlate ? 'error' : 'success' : null }
            extra={ veiculo.errors ? veiculo.errors.vehiclePlate ? <ErroPersonalizado msg={veiculo.errors.vehiclePlate} /> : null : null }
          >
            <Input
              id='input-placaVeiculo'
              style={{ fontFamily: "'Open Sans', sans-serif" }}
              value={veiculo.vehiclePlate}
              onChange={e => handleChange(e.target.value, trigger.PLACA_DO_VEICULO)}
            />
          </Form.Item>
        </Col>
        <Col md={24}>
          <Divider />
          <h2 className='questionH2'>TEM OU TEVE ALGUM SEGURO?</h2>
          <Radio.Group
            onChange={(e) => handleChange(e.target.value, trigger.TEM_SEGURO)}
            value={veiculo.temSeguro}
            className='radionGroupFormVeiculo'
          >
            <Row gutter={[32, 32]} type='flex' align='middle'>
              <Col span={12}>
                <Radio.Button
                  value={true}
                  className={veiculo.temSeguro === true ? 'radio-button-form-veiculo-checked' : 'radio-button-form-veiculo'}
                >
                  <div className={veiculo.temSeguro === true ? 'radio-button-form-label-veiculo-checked' : 'radio-button-form-label-veiculo'}>
                                        SIM
                  </div>
                </Radio.Button>
              </Col>
              <Col span={12}>
                <Radio.Button
                  value={false}
                  className={veiculo.temSeguro === false ? 'radio-button-form-veiculo-checked' : 'radio-button-form-veiculo'}
                >
                  <div className={veiculo.temSeguro === false ? 'radio-button-form-label-veiculo-checked' : 'radio-button-form-label-veiculo'}>
                                        NÃO
                  </div>
                </Radio.Button>
              </Col>
              <Col span={24} style={{ padding: '0px 16px', textAlign: 'start' }}>
                { veiculo.errors ? veiculo.errors.temSeguro ? <ErroPersonalizado msg={veiculo.errors.temSeguro} /> : null : null }
              </Col>
            </Row>
          </Radio.Group>
        </Col>
        {
          veiculo.temSeguro
            ? <>
              <Col md={24} style={{ marginTop: '40px' }}>
                <h2 className='questionH2'>SEU SEGURO AINDA ESTÁ ATIVO?</h2>
                <Radio.Group
                  onChange={(e) => handleChange(e.target.value, trigger.POSSUI_SEGURO_ATIVO)}
                  value={veiculo.possuiSeguroAtivo}
                  className='radionGroupFormVeiculo'
                >
                  <Row gutter={[32, 32]}>
                    <Col span={12}>
                      <Radio.Button
                        value={true}
                        className={veiculo.possuiSeguroAtivo === true ? 'radio-button-form-veiculo-checked' : 'radio-button-form-veiculo'}
                      >
                        <div style={{ color: '#212529', fontWeight: 600, fontSize: '16px' }}>SIM</div>
                      </Radio.Button>
                    </Col>
                    <Col span={12}>
                      <Radio.Button
                        value={false}
                        className={veiculo.possuiSeguroAtivo === false ? 'radio-button-form-veiculo-checked' : 'radio-button-form-veiculo'}
                      >
                        <div style={{ color: '#212529', fontWeight: 600, fontSize: '16px' }}>NÃO</div>
                      </Radio.Button>
                    </Col>
                    { veiculo.errors ? veiculo.errors.possuiSeguroAtivo ? <ErroPersonalizado msg={veiculo.errors.possuiSeguroAtivo} /> : null : null }
                  </Row>
                </Radio.Group>
                <Divider />
              </Col>
              <Col md={24}>
                <Form.Item
                  label={<label className="labelFormVeiculo" htmlFor='input-precoSeguro'>QUANTO PAGA/PAGOU PELO SEGURO?</label>}
                  required={veiculo.temSeguro}
                  hasFeedback={veiculo.precoSeguro}
                  validateStatus={veiculo.errors ? veiculo.errors.precoSeguro ? 'error' : 'success' : null }
                  colon={false}
                  extra={ veiculo.errors ? veiculo.errors.precoSeguro ? <ErroPersonalizado msg={veiculo.errors.precoSeguro} /> : null : null }
                >
                  <Input
                    value={veiculo.precoSeguro}
                    id='input-precoSeguro'
                    style={{ fontFamily: "'Open Sans', sans-serif" }}
                    onChange={e => {
                      let valor = e.target.value
                      valor = valor.replace(/\D/g, '')
                      handleChange(valor, trigger.PRECO_SEGURO)
                    }}
                    placeholder='Apenas números inteiros'
                  />
                </Form.Item>
              </Col>
              <Col md={24}>
                <Form.Item
                  label={<label className="labelFormVeiculo" htmlFor='input-ciaSeguradora'>QUAL É/FOI A CIA SEGURADORA?</label>}
                  colon={false}
                  required={veiculo.temSeguro}
                  hasFeedback={veiculo.ciaSeguradora}
                  validateStatus={veiculo.errors ? veiculo.errors.ciaSeguradora ? 'error' : 'success' : null }
                  extra={ veiculo.errors ? veiculo.errors.ciaSeguradora ? <ErroPersonalizado msg={veiculo.errors.ciaSeguradora} /> : null : null }
                >
                  <Input
                    value={veiculo.ciaSeguradora}
                    id='input-ciaSeguradora'
                    style={{ fontFamily: "'Open Sans', sans-serif" }}
                    onChange={e => handleChange(e.target.value, trigger.CIA_SEGURADORA)}
                    placeholder='Porto Seguro, HDI, Mapfre...'
                  />
                </Form.Item>
              </Col>
              <Col md={24}>
                <Form.Item
                  label={<label className="labelFormVeiculo" htmlFor='input-fimContratoSeguradora'>QUANDO TERMINA SEU CONTRATO?</label>}
                  colon={false}
                  required={true}
                  validateStatus={veiculo.errors ? veiculo.errors.fimContrato ? 'error' : 'success' : null }
                  extra={ veiculo.errors ? veiculo.errors.fimContrato ? <ErroPersonalizado msg={veiculo.errors.fimContrato} /> : null : null }
                >
                  <DatePicker
                    disabled={!veiculo.possuiSeguroAtivo}
                    id='input-fimContratoSeguradora'
                    value={veiculo.fimContrato ? moment(veiculo.fimContrato) : null}
                    onChange={(date, dateString) => {
                      const arrayDate = dateString.split('/')
                      const dayFormat = `${arrayDate[2]}-${arrayDate[1]}-${arrayDate[0]}`
                      moment(date).isValid()
                        ? (handleChange(moment(dayFormat), trigger.FIM_CONTRATO))
                        : (handleChange(null, trigger.FIM_CONTRATO))
                    }}
                    placeholder='Selecione a data'
                    format='DD/MM/YYYY'
                    style={{ width: '100%', fontFamily: "'Open Sans', sans-serif" }}
                    locale={DateConfig}
                  />
                </Form.Item>
              </Col>
            </>
            : null
        }
      </Row>
    </>
  )
}

const mapDispatchToProps = dispatch => ({
  handleChange: (item, tipo) => dispatch(handleChange(item, tipo))
})

const mapStateToProps = createStructuredSelector({
  veiculo: selectFormularioVeiculo
})

export default connect(mapStateToProps, mapDispatchToProps)(Veiculo)
