/* eslint-disable react/prop-types */
import React from 'react'
import { Row, Col, Form, Input } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMobileAlt, faEnvelope } from '@fortawesome/free-solid-svg-icons'
import { selectUsuario } from '../../../redux/formulario/formulario.selector'
import { connect } from 'react-redux'
import { handleChange } from '../../../redux/formulario/formulario.actions'
import { trigger } from '../../../redux/formulario/formulario.utils'
import { createStructuredSelector } from 'reselect'
import { ErroPersonalizado, Jumbotrom } from '../..'

import './style.css'

/**
 * Componente do Formulário, parte 4.
 * Nessa seção é coletada todas as informações pessoais necessárias
 * do usuário.
 * Todos os campos são obrigatórios.
 */
// eslint-disable-next-line react/prop-types
const SobreVoce = ({ usuario, handleChange }) => {
  return (
    <Row gutter={32}>
      <Col md={24}>
        <Jumbotrom>
          <p className='jumbotron-paragraph-form-sobre'>
                        Seu interesse na inovação é o que nos move.
                        Precisamos de algumas informações sobre você para prestar um atendimento melhor.
          </p>
        </Jumbotrom>
      </Col>
      <Col md={12}>
        <Form.Item
          label={<label className='label-form-sobre' htmlFor='input-nomeUsuario'>QUAL É O SEU NOME?</label>}
          colon={false}
          required
          hasFeedback
          validateStatus={usuario.errors ? usuario.errors.nome ? 'error' : 'success' : null }
          extra={ usuario.errors ? usuario.errors.nome ? <ErroPersonalizado msg={usuario.errors.nome} /> : null : null }
        >
          <Input
            value={usuario.nome}
            id='input-nomeUsuario'
            style={{ fontFamily: "'Open Sans', sans-serif" }}
            onChange={e => {
              handleChange(e.target.value, trigger.USUARIO_NOME)
            }}
          />
        </Form.Item>
      </Col>
      <Col md={12}>
        <Form.Item
          label={<label className='label-form-sobre' htmlFor='input-sobrenomeUsuario'>QUAL É O SEU SOBRENOME?</label>}
          colon={false}
          required
          hasFeedback
          validateStatus={usuario.errors ? usuario.errors.sobreNome ? 'error' : 'success' : null }
          extra={ usuario.errors ? usuario.errors.sobreNome ? <ErroPersonalizado msg={usuario.errors.sobreNome} /> : null : null }
        >
          <Input
            value={usuario.sobreNome}
            id='input-sobrenomeUsuario'
            style={{ fontFamily: "'Open Sans', sans-serif" }}
            onChange={e => {
              handleChange(e.target.value, trigger.USUARIO_SOBRENOME)
            }}
          />
        </Form.Item>
      </Col>
      <Col md={12}>
        <Form.Item
          label={<label className='label-form-sobre' htmlFor='input-telefoneUsuario'>TELEFONE</label>}
          required
          hasFeedback
          validateStatus={usuario.errors ? usuario.errors.telefone ? 'error' : 'success' : '' }
          extra={ usuario.errors ? usuario.errors.telefone ? <ErroPersonalizado msg={usuario.errors.telefone} /> : null : null }
        >
          <Input
            prefix={<FontAwesomeIcon icon={faMobileAlt} style={{ color: '#1890ff' }} />}
            value={usuario.telefone}
            id='input-telefoneUsuario'
            style={{ fontFamily: "'Open Sans', sans-serif" }}
            onChange={e => {
              const celular = e.target.value
                .replace(/\D/g, '')
                .replace(/(\d{2})(\d)/, '($1) $2')
                .replace(/(\d{4})(\d)/, '$1-$2')
                .replace(/(\d{4})-(\d)(\d{4})/, '$1$2-$3')
                .replace(/(-\d{4})\d+?$/, '$1')
              handleChange(celular, trigger.USUARIO_TELEFONE)
            }}
          />
        </Form.Item>
      </Col>
      <Col md={12}>
        <Form.Item
          label={<label className='label-form-sobre' htmlFor='input-cepUsuario'>CEP</label>}
          required
          hasFeedback
          validateStatus={usuario.errors ? usuario.errors.cep ? 'error' : 'success' : null }
          extra={ usuario.errors ? usuario.errors.cep ? <ErroPersonalizado msg={usuario.errors.cep} /> : null : null }
        >
          <Input
            value={usuario.cep}
            id='input-cepUsuario'
            style={{ fontFamily: "'Open Sans', sans-serif" }}
            onChange={e => {
              const cep = e.target.value
                .replace(/\D/g, '')
                .replace(/(\d{5})(\d)/, '$1-$2')
                .replace(/(-\d{3})\d+?$/, '$1')
              handleChange(cep, trigger.USUARIO_CEP)
            }}
          />
        </Form.Item>
      </Col>
      <Col md={24}>
        <Form.Item
          label={<label className='label-form-sobre' htmlFor='input-emailUsuario'>EMAIL</label>}
          colon={false}
          required
          hasFeedback
          validateStatus={usuario.errors ? usuario.errors.email ? 'error' : 'success' : null }
          extra={ usuario.errors ? usuario.errors.email ? <ErroPersonalizado msg={usuario.errors.email} /> : null : null }
        >
          <Input
            prefix={<FontAwesomeIcon icon={faEnvelope} style={{ color: '#1890ff' }} />}
            value={usuario.email}
            style={{ fontFamily: "'Open Sans', sans-serif" }}
            id='input-emailUsuario'
            onChange={e => {
              handleChange(e.target.value, trigger.USUARIO_EMAIL)
            }}
          />
        </Form.Item>
      </Col>
    </Row>
  )
}

const mapDispatchToProps = dispatch => ({
  handleChange: (item, tipo) => dispatch(handleChange(item, tipo))
})

const mapStateToProps = createStructuredSelector({
  usuario: selectUsuario
})

export default connect(mapStateToProps, mapDispatchToProps)(SobreVoce)
