/* eslint-disable no-case-declarations */
import { verificaStep, trigger } from './formulario.utils'
import { proximoStep, stepAnterior, isEmptyObject } from './formulario.actions'
import { notification } from 'antd'

const INITIAL_STATE = {
  veiculo: {
    marca: '',
    tipo_de_veiculo: '',
    fabricacao: '',
    qtd: null,
    total: null,
    vehiclePlate: '',
    temSeguro: null,
    possuiSeguroAtivo: null,
    precoSeguro: '',
    ciaSeguradora: '',
    fimContrato: ''
  },
  onsurance: {
    horas_por_dia: ['01', '00'],
    assistencia_onsurance: null
  },
  usuario: {
    nome: '',
    sobreNome: '',
    telefone: '',
    cep: '',
    email: ''
  },
  results: {},
  step: 0,
  loading: false
}

const Notificar = () => notification.error({
  message: 'Erro(s) no formulário',
  description: 'Erro ao submeter as insformações.',
  duration: 2
})

const formularioReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case trigger.PROXIMO_STEP:
      return {
        ...state,
        step: proximoStep(state.step)
      }

    case trigger.PROXIMO_STEP_1:
      const errors = verificaStep(state.step, state.veiculo)
      if (!isEmptyObject(errors)) {
        Notificar()
        return {
          ...state,
          veiculo: {
            ...state.veiculo,
            errors: errors
          }
        }
      }
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          errors: {}
        },
        step: proximoStep(state.step)
      }

    case trigger.PROXIMO_STEP_2:
      const errors2 = verificaStep(state.step, state.onsurance)
      if (!isEmptyObject(errors2)) {
        Notificar()
        return {
          ...state,
          onsurance: {
            ...state.onsurance,
            errors: errors2
          }
        }
      }
      return {
        ...state,
        onsurance: {
          ...state.onsurance,
          errors: {}
        },
        step: proximoStep(state.step)
      }

    case trigger.PROXIMO_STEP_3:
      const errors3 = verificaStep(state.step, state.usuario)
      if (!isEmptyObject(errors3)) {
        Notificar()
        return {
          ...state,
          usuario: {
            ...state.usuario,
            errors: errors3
          }
        }
      }
      return {
        ...state,
        usuario: {
          ...state.usuario,
          errors: {}
        },
        step: proximoStep(state.step)
      }

    case trigger.STEP_ANTERIOR:
      return {
        ...state,
        step: stepAnterior(state.step)
      }

    case trigger.MARCA_PNEU:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          marca: action.payload
        }
      }

    case trigger.PLACA_DO_VEICULO:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          vehiclePlate: action.payload
        }
      }

    case trigger.VALOR_TOTAL_PNEU:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          total: action.payload
        }
      }

    case trigger.QUANTIDADE_PNEU:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          qtd: action.payload
        }
      }

    case trigger.TIPO_DE_VEICULO:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          tipo_de_veiculo: action.payload
        }
      }

    case trigger.TIPO_FABRICACAO:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          fabricacao: action.payload
        }
      }

    case trigger.ON_HORAS_POR_DIA:
      return {
        ...state,
        onsurance: {
          ...state.onsurance,
          horas_por_dia: action.payload
        }
      }

    case trigger.ASSISTENCIA_ONSURANCE:
      return {
        ...state,
        onsurance: {
          ...state.onsurance,
          assistencia_onsurance: action.payload
        }
      }

    case trigger.USUARIO_NOME:
      return {
        ...state,
        usuario: {
          ...state.usuario,
          nome: action.payload
        }
      }

    case trigger.USUARIO_SOBRENOME:
      return {
        ...state,
        usuario: {
          ...state.usuario,
          sobreNome: action.payload
        }
      }

    case trigger.USUARIO_TELEFONE:
      return {
        ...state,
        usuario: {
          ...state.usuario,
          telefone: action.payload
        }
      }

    case trigger.USUARIO_CEP:
      return {
        ...state,
        usuario: {
          ...state.usuario,
          cep: action.payload
        }
      }

    case trigger.USUARIO_EMAIL:
      return {
        ...state,
        usuario: {
          ...state.usuario,
          email: action.payload
        }
      }

    case trigger.RESULTADO_FINAL:
      return {
        ...state,
        results: action.payload
      }

    case trigger.SET_LOADING:
      return {
        ...state,
        loading: action.payload
      }

    case trigger.TEM_SEGURO:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          temSeguro: action.payload
        }
      }

    case trigger.PRECO_SEGURO:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          precoSeguro: action.payload
        }
      }

    case trigger.POSSUI_SEGURO_ATIVO:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          possuiSeguroAtivo: action.payload
        }
      }

    case trigger.CIA_SEGURADORA:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          ciaSeguradora: action.payload
        }
      }

    case trigger.FIM_CONTRATO:
      return {
        ...state,
        veiculo: {
          ...state.veiculo,
          fimContrato: action.payload
        }
      }

    default:
      return state
  }
}

export default formularioReducer
