import React from 'react'

import './style.css'

const DIRECTION = {
  default: 'center-div',
  column: 'center-div-column'
}

/**
 * Esse componente possui os estilos para alinhar o item no centro
 * e pode ser usado na direção Row como pode ser usado como coluna.
 */
// eslint-disable-next-line react/prop-types
const CenterDiv = ({ children, direction = 'default' }) => {
  return (
    <div className={DIRECTION[direction]}>
      {children}
    </div>
  )
}

export default CenterDiv
