/* eslint-disable react/prop-types */
import React from 'react'
import { trigger } from '../../../redux/formulario/formulario.utils'
import { Button, message } from 'antd'

// eslint-disable-next-line react/prop-types
const ProximoButton = ({ step, irProximoStep, handleChange, state, loading }) => {
  return (
    <Button onClick={async () => {
      switch (step) {
        case 0: {
          await irProximoStep(trigger.PROXIMO_STEP)
          break
        }
        case 1: {
          await irProximoStep(trigger.PROXIMO_STEP_1)
          break
        }
        case 2: {
          await irProximoStep(trigger.PROXIMO_STEP_2)
          break
        }
        case 4: {
          window.location.href = 'https://loja.onsurance.me/'
          break
        }
        default: {
          handleChange(true, trigger.SET_LOADING)
          const requestOptions = {
            method: 'GET',
            redirect: 'follow'
          }
          const telefone = state.usuario.telefone.replace(/\D/g, '')
          let dataFormater = state.veiculo.fimContrato
          if (state.veiculo.fimContrato._i) {
            dataFormater = state.veiculo.fimContrato._i
            dataFormater = dataFormater.split('-')
            dataFormater = `${dataFormater[2]}/${dataFormater[1]}/${dataFormater[0]}`
          } else {
            dataFormater = 'none'
          }

          const params = {
            totalValue: Number.parseInt(state.veiculo.total),
            qtd: Number.parseInt(state.veiculo.qtd),
            tireBrand: state.veiculo.marca,
            tireFactory: state.veiculo.fabricacao,
            vehicleType: state.veiculo.tipo_de_veiculo,
            firstName: state.usuario.nome,
            lastName: state.usuario.sobreNome,
            userEmail: state.usuario.email,
            vehiclePlate: state.veiculo.vehiclePlate,
            dailyUsage: `${state.onsurance.horas_por_dia[0]},${state.onsurance.horas_por_dia[1]}`,
            phone: telefone,
            insuranceOwner: state.veiculo.temSeguro,
            insuranceValue: state.veiculo.precoSeguro,
            activeInsurance: state.veiculo.possuiSeguroAtivo,
            insuranceCompany: state.veiculo.ciaSeguradora,
            insuranceExpiration: dataFormater,
            zip: state.usuario.cep
          }
          const url = `https://api.onsurance.me/quote/tires?totalValue=${params.totalValue}&qtd=${params.qtd}&tireBrand=${params.tireBrand}&tireFactory=${params.tireFactory}&vehicleType=${params.vehicleType}&vehiclePlate=${params.vehiclePlate}&firstName=${params.firstName}&lastName=${params.lastName}&userEmail=${params.userEmail}&dailyUsage=${params.dailyUsage}&phone=${params.phone}&insuranceOwner=${params.insuranceOwner}&insuranceValue=${params.insuranceValue}&activeInsurance=${params.activeInsurance}&insuranceCompany=${params.insuranceCompany}&insuranceExpiration=${params.insuranceExpiration}&zip=${params.zip}`
          await fetch(url, requestOptions)
            .then(response => response.json())
            .then(result => {
              handleChange(false, trigger.SET_LOADING)
              handleChange(result, trigger.RESULTADO_FINAL)
              irProximoStep(trigger.PROXIMO_STEP_3)
            })
            .catch(e => message.error('Houve uma falha na comunicação com o servidor, tente mais tarde.', 3, () => handleChange(false, trigger.SET_LOADING)))
          break
        }
      }
      const card = document.getElementById('cardPrincipal')
      if (step !== 4) {
        window.scrollTo({ top: card.offsetTop, behavior: 'smooth' })
      }
    }}
    style={{ fontFamily: "'Open Sans', sans-serif" }}
    type="link"
    loading={loading}
    block
    id={`buttonProxima${step}`}
    >{step === 4 ? 'CONTRATE AGORA!' : 'PRÓXIMO' }</Button>
  )
}

export default ProximoButton
