import React from 'react'
import { connect } from 'react-redux'
import { createStructuredSelector } from 'reselect'
import { selectStep, selectFormulario, SelectLoading } from '../../redux/formulario/formulario.selector'
import { Card, Steps, Col, Row} from 'antd'
import { irProximoStep, irStepAnterior, handleChange } from '../../redux/formulario/formulario.actions'
import { Veiculo, Cotacao, EntendaOnsurnance, SobreVoce, ResultadoFormulario, StepsMobile, AnteriorButton, ProximoButton, TermoDeUso } from '../../components'
import { withBreakpoints } from 'react-breakpoints'

import './style.css'

const { Step } = Steps

const steps = [
  {
    title: 'Cotação',
    header: 'Cotação Onsurance',
    content: <Cotacao />
  },
  {
    title: 'Pneu',
    header: 'Dados do pneu',
    content: <Veiculo />
  },
  {
    title: 'Onsurance',
    header: 'Entender seu uso Onsurance',
    content: <EntendaOnsurnance />
  },
  {
    title: 'Sobre você',
    header: 'Conte-nos sobre você',
    content: <SobreVoce />
  },
  {
    title: 'Resultado',
    header: 'Resultado da cotação',
    content: <ResultadoFormulario />
  }
]

const styles = [
  {
    width: '100%',
    heigth: 'auto',
    maxWidth: '900px',
    borderRadius: '10px'
  },
  {
    textAlign: 'center',
    marginBottom: '20px',
    fontWeight: 600,
    fontSize: '24px'
  },
  {
    display: 'block',
    marginLeft: 'auto',
    marginRight: 'auto',
    width: '40%'
  }
]

// eslint-disable-next-line react/prop-types
const IndexComponent = ({ step, irProximoStep, irStepAnterior, state, handleChange, loading, currentBreakpoint }) => {
  return (
    <div className='conteudo'>
      {
        step === 5
          ? (<ResultadoFormulario />)
          : <>
            <Row>
              {
                currentBreakpoint === 'mobile' ||
                                currentBreakpoint === 'mobileLandscape'
                  ? <StepsMobile step={step + 1} title={steps[step].title} />
                  : <Col>
                    <Steps current={step} style={{ marginBottom: '30px', padding: '0 50px 0 50px' }}>
                      {steps.map(item => (
                        <Step key={item.title} title={item.title} />
                      ))}
                    </Steps>
                  </Col>
              }
            </Row>
            <div className='centralizar'>
              <Card
                className='card-main'
                style={styles[0]}
                id='cardPrincipal'
                bordered={false}
                bodyStyle={
                  currentBreakpoint === 'mobile' || currentBreakpoint === 'mobileLandscape'
                    ? { padding: '24px 16px' }
                    : { padding: '24px 80px' }
                }
                actions={
                  step !== 4
                    ? [
                      <AnteriorButton key={'anteriorButton'} step={step} irStepAnterior={irStepAnterior} />,
                      <ProximoButton key={'proximoButton'} step={step} handleChange={handleChange} irProximoStep={irProximoStep} state={state} loading={loading} />
                    ]
                    : [
                      <AnteriorButton key={'anteriorButton'} step={step} irStepAnterior={irStepAnterior} />,
                      <TermoDeUso key={'termoDeUsoButton'} />,
                      <ProximoButton key={'proximoButton'} step={step} handleChange={handleChange} irProximoStep={irProximoStep} state={state} loading={loading} />
                    ]
                }
              >
                <h2 style={styles[1]}>{steps[step].header.toUpperCase()}</h2>
                {
                  steps[step].content
                }
              </Card>
            </div>
          </>
      }
    </div>
  )
}

const mapDispatchToProps = dispatch => ({
  irProximoStep: item => dispatch(irProximoStep(item)),
  irStepAnterior: item => dispatch(irStepAnterior(item)),
  handleChange: (item, tipo) => dispatch(handleChange(item, tipo))
})

const mapStateToProps = createStructuredSelector({
  step: selectStep,
  state: selectFormulario,
  loading: SelectLoading
})

export default connect(mapStateToProps, mapDispatchToProps)(withBreakpoints(IndexComponent))
