/* eslint-disable react/prop-types */
import React from 'react'
import { Row, Col, Form, TimePicker, Divider } from 'antd'
import { TempoResult, RadioButtonOnsurance, ErroPersonalizado, Jumbotrom, CenterDiv } from '../..'
import { connect } from 'react-redux'
import { createStructuredSelector } from 'reselect'
import { handleChange } from '../../../redux/formulario/formulario.actions'
import { trigger } from '../../../redux/formulario/formulario.utils'
import { selectOnsurance } from '../../../redux/formulario/formulario.selector'
import { withBreakpoints } from 'react-breakpoints'
import moment from 'moment'

import './styles.css'

const styles = [
  {
    textAlign: 'center',
    margin: '20px 0 20px 0',
    fontWeight: 600,
    fontSize: '24px'
  }
]

const data = [
  'Guincho',
  'Reboque',
  'Chaveiro',
  'Táxi/Uber'
]
const data2 = [
  'Pane Seca/Mecânica/Elétrica',
  'Troca de pneus',
  'Guarda veículo',
  'Transmissão de mensagens'
]

/**
 * Componente do Formulário, parte 3.
 * Representa as horas diárias de uso do seguro e os acessórios
 * Todos os campos são obrigatórios.
 */
// eslint-disable-next-line react/prop-types
const EntendaOnsurnance = ({ onsurance, handleChange, currentBreakpoint }) => {
  return (
    <Row gutter={32}>
      <Col span={24}>
        <Jumbotrom>
          <p className='jumbotronParagraph'>
                        O Onsurance pneus tem as seguintes coberturas:
          </p>
          <ul className='jumbotronParagraph'>
            <li>Reparo ou substituição do pneu em caso de Furos, Rasgos ou Estouros.</li>
          </ul>
          <p className='jumbotronParagraph'>
                        Cobertura integral (até 100% do valor de Nota fiscal) contra rasgos,
                        estouro e avarias que impeçam seu pneu de continuar rodando. 100% do
                        valor do concerto para pneus furados. Troca de pneus em caso de avarias
                        e guincho em caso de impossibilidade de continuar o
                        trajeto em detrimento de avarias nos pneus segurados*.
          </p>
          <i className='jumbotronParagraph' style={{ fontWeight: 200 }}>
                        *Tenha em mente que a cobertura só vale caso o pneu ainda esteja dentro da marca de segurança (TWI).
          </i>
        </Jumbotrom>
      </Col>
      <Col span={24}>
        <CenterDiv direction='column'>
          <label
            className='paragraph-font-properties'
            style={{ textAlign: 'center', margin: '15px 0 15px 0' }}
            htmlFor='input-horasDiarias'
          >
                        SELECIONE QUANTAS HORAS POR DIA VOCÊ ESTIMA RODAR COM O SEU VEÍCULO:
          </label>
          <Form.Item
            extra={ onsurance.errors ? onsurance.errors.horas_por_dia ? <ErroPersonalizado msg={onsurance.errors.horas_por_dia} /> : null : null }
          >
            <CenterDiv>
              <TimePicker
                focusOnOpen
                size='large'
                id='input-horasDiarias'
                defaultValue={moment('01:00', 'HH:mm')}
                allowClear={false}
                onChange={(e, item) => {
                  const valor = item.split(':')
                  handleChange(valor, trigger.ON_HORAS_POR_DIA)
                }}
                value={moment(`${onsurance.horas_por_dia[0]}:${onsurance.horas_por_dia[1]}`, 'HH:mm')}
                format={'HH:mm'}
              />
            </CenterDiv>
          </Form.Item>
        </CenterDiv>
      </Col>
      <Col xs={24} sm={12} md={8}>
        <CenterDiv>
          <TempoResult title='Horas/ Semana' horas={onsurance.horas_por_dia} dias={7} />
        </CenterDiv>
      </Col>
      <Col xs={24} sm={12} md={8}>
        <CenterDiv>
          <TempoResult title='Horas/ Mês' horas={onsurance.horas_por_dia} dias={30} />
        </CenterDiv>
      </Col>
      <Col xs={24} sm={12} md={8}>
        <CenterDiv>
          <TempoResult title='Horas/ Ano' horas={onsurance.horas_por_dia} dias={365} />
        </CenterDiv>
      </Col>
      <Col span={24}>
        <Divider />
        <h2 style={styles[0]}>ASSISTÊNCIA 24H</h2>
      </Col>
      <Col span={24}>
        <Jumbotrom>
          <p className='jumbotronParagraph'>
                        Na Onsurance a <strong style={{ fontWeight: 'bolder' }}>Assistência 24h</strong> é uma assinatura anual.
          </p>
          <p className='jumbotronParagraph'>
                        Você terá direito a 3 utilizações com 300 km de rodagem (ida/volta) para os serviços:
          </p>
          <Row gutter={32}>
            <Col xs={24} sm={12}>
              <ul style={{ margin: '0 0 1rem 0' }}>
                { data.map(item => <li key={data.indexOf(item)}className='paragraph-font-properties'>{item}</li>) }
              </ul>
            </Col>
            <Col xs={24} sm={12}>
              <ul style={{ margin: '0 0 1rem 0' }}>
                { data2.map(item => <li key={data2.indexOf(item)}className='paragraph-font-properties'>{item}</li>) }
              </ul>
            </Col>
          </Row>
        </Jumbotrom>
      </Col>
      <Col span={24}>
        <Form.Item
          label={<label className='paragraph-font-properties' htmlFor='input-assistencia'>
                        A ASSINATURA DA ASSISTÊNCIA 24H CUSTA R$ 249,00/ANO. DESEJA INCLUIR NA COTAÇÃO?
          </label>}
          colon={false}
          required
          extra={ onsurance.errors ? onsurance.errors.assistencia_onsurance ? <ErroPersonalizado msg={onsurance.errors.assistencia_onsurance} /> : null : null }
        >
          {
            currentBreakpoint === 'mobileLandscape'
              ? <RadioButtonOnsurance
                width='100%'
                id='input-assistencia'
                funcao={trigger.ASSISTENCIA_ONSURANCE}
                value={onsurance.assistencia_onsurance}
              />
              : <RadioButtonOnsurance
                id='input-assistencia'
                funcao={trigger.ASSISTENCIA_ONSURANCE}
                value={onsurance.assistencia_onsurance}
              />
          }
        </Form.Item>
      </Col>
    </Row>
  )
}

const mapDispatchToProps = dispatch => ({
  handleChange: (item, tipo) => dispatch(handleChange(item, tipo))
})

const mapStateToProps = createStructuredSelector({
  onsurance: selectOnsurance
})

export default connect(mapStateToProps, mapDispatchToProps)(withBreakpoints(EntendaOnsurnance))
