import React from 'react'
import { Radio } from 'antd'
import { connect } from 'react-redux'
import { handleChange } from '../../../redux/formulario/formulario.actions'

import './style.css'

/**
 * Esse componente representa o Radio Group do formulário 3(Onsurance).
 */
// eslint-disable-next-line react/prop-types
const RadioButtonOnsurance = ({ handleChange, funcao, value, width = '100px' }) => {
  return (
    <Radio.Group
      onChange={e => handleChange(e.target.value, funcao)}
      value={value}
      buttonStyle='outline'
      style={{ width: '100%' }}
    >
      <div style={{
        display: 'flex'
      }}>
        <Radio.Button
          value={true}
          style={{ width: width, margin: '0 20px 0 0' }}
          className={value === true ? 'radio-button-form-onsurance-checked' : 'radio-button-form-onsurance'}
        >
          <div>SIM</div>
        </Radio.Button>
        <Radio.Button
          value={false}
          style={{ width: width }}
          className={value === false ? 'radio-button-form-onsurance-checked' : 'radio-button-form-onsurance'}
        >
          <div>NÃO</div>
        </Radio.Button>
      </div>
    </Radio.Group>
  )
}

const mapDispatchToProps = dispatch => ({
  handleChange: (item, tipo) => dispatch(handleChange(item, tipo))
})

export default connect(null, mapDispatchToProps)(RadioButtonOnsurance)
