import React from 'react'
import { Radio } from 'antd'

import './style.css'

/**
 * Esse componente representa 1 label no formulário 2(Veiculos) para a opção
 * do tipo de veículo.
 */
// eslint-disable-next-line react/prop-types
const RadioVeiculo = ({ value, icone, alt, label, checked = false }) => {
  return (
    <Radio.Button
      value={value}
      className={checked ? 'radio-button-veiculo-checked' : 'radio-button-veiculo'}
    >
      <img
        src={icone}
        alt={alt}
        width={60}
      />
      <div className={checked ? 'div-label-radio-veiculo-checked' : 'div-label-radio-veiculo'}>
        {label}
      </div>
    </Radio.Button>
  )
}

export default RadioVeiculo
