import React from 'react'
import { Statistic, Card } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faClock } from '@fortawesome/free-solid-svg-icons'

// eslint-disable-next-line react/prop-types
const TempoResult = ({ title, dias, horas }) => {
  let horasFormatadas = parseInt(horas[0]) * 60 + parseInt(horas[1])
  const minutosPorDias = horasFormatadas * dias
  horasFormatadas = Math.floor(minutosPorDias / 60)
  const novaV = horasFormatadas * 60
  const minutosFormatados = minutosPorDias - novaV

  return (
    <Card bordered={false}>
      <Statistic
        title={title}
        value={`${horasFormatadas}h ${minutosFormatados !== 0 ? `${minutosFormatados}m` : ''}`}
        // value={`${dias}h`}
        prefix={<FontAwesomeIcon icon={faClock}
          style={{ color: '#1890ff' }} />}
      />
    </Card>
  )
}

export default TempoResult
