import React from 'react'

const styles = {
  color: '#e57373',
  fontSize: '14px'
}

/**
 * Esse componente exibe as mensagem de erro dos campos que possuem algum
 * erro de validação.
 */
// eslint-disable-next-line react/prop-types
const ErroPersonalizado = ({ msg }) => <p style={styles}>{msg}</p>

export default ErroPersonalizado
