export const trigger = {
  PROXIMO_STEP: 'PROXIMO_STEP',
  PROXIMO_STEP_1: 'PROXIMO_STEP_1',
  PROXIMO_STEP_2: 'PROXIMO_STEP_2',
  STEP_ANTERIOR: 'STEP_ANTERIOR',
  SET_LOADING: 'SET_LOADING',
  RESULTADO_FINAL: 'RESULTADO_FINAL',
  PROXIMO_STEP_3: 'PROXIMO_STEP_3',
  MARCA_PNEU: 'MARCA_PNEU',
  TIPO_FABRICACAO: 'TIPO_FABRICACAO',
  TIPO_DE_VEICULO: 'TIPO_DE_VEICULO',
  VALOR_TOTAL_PNEU: 'VALOR_TOTAL_PNEU',
  QUANTIDADE_PNEU: 'QUANTIDADE_PNEU',
  PLACA_DO_VEICULO: 'PLACA_DO_VEICULO',
  TEM_SEGURO: 'TEM_SEGURO',
  POSSUI_SEGURO_ATIVO: 'POSSUI_SEGURO_ATIVO',
  PRECO_SEGURO: 'PRECO_SEGURO',
  CIA_SEGURADORA: 'CIA_SEGURADORA',
  FIM_CONTRATO: 'FIM_CONTRATO',
  ON_HORAS_POR_DIA: 'ON_HORAS_POR_DIA',
  ASSISTENCIA_ONSURANCE: 'ASSISTENCIA_ONSURANCE',
  USUARIO_NOME: 'USUARIO_NOME',
  USUARIO_SOBRENOME: 'USUARIO_SOBRENOME',
  USUARIO_TELEFONE: 'USUARIO_TELEFONE',
  USUARIO_CEP: 'USUARIO_CEP',
  USUARIO_EMAIL: 'USUARIO_EMAIL'
}

export const verificaStep = (stepNumber, stepValue) => {
  let errors = {}
  switch (stepNumber) {
    case 1:
      if (stepValue.marca === '') {
        errors = { ...errors, marca: 'A marca do veículo deve ser informada!' }
      }
      if (stepValue.tipo_de_veiculo === '') {
        errors = { ...errors, tipo_de_veiculo: 'É necessário informar o tipo do veículo!' }
      }
      if (stepValue.fabricacao === '') {
        errors = { ...errors, fabricacao: 'Você deve informar o tipo de fabricação do pneu!' }
      }
      if (stepValue.qtd === null || stepValue.qtd === '') {
        errors = { ...errors, qtd: 'É Obrigatório informar a quantidade de pneus!' }
      }
      if (stepValue.total === null || stepValue.total === '') {
        errors = { ...errors, total: 'É obrigatório informar o valor total do pneu!' }
      }
      if (stepValue.vehiclePlate === '') {
        errors = { ...errors, vehiclePlate: 'É Obrigatório informar a placa do seu veículo!' }
      }
      if (stepValue.temSeguro === null) {
        errors = { ...errors, temSeguro: 'É Obrigatório informar se você ja teve algum seguro de pneu!' }
      }
      if (stepValue.temSeguro === true) {
        if (stepValue.possuiSeguroAtivo === null) {
          errors = { ...errors, possuiSeguroAtivo: 'É Obrigatório informar se este seguro está ativo!' }
        } else if (stepValue.possuiSeguroAtivo === true) {
          if (stepValue.fimContrato === '') {
            errors = { ...errors, fimContrato: 'É necessário informar quando termina seu contrato!' }
          }
        }
        if (stepValue.precoSeguro === '') {
          errors = { ...errors, precoSeguro: 'O preço do seu antigo/atual seguro deve ser informado!' }
        }
        if (stepValue.ciaSeguradora === '') {
          errors = { ...errors, ciaSeguradora: 'É necessário informar qual a cia seguradora!' }
        }
      }
      break
    case 2:
      if ((stepValue.horas_por_dia[0] === '00' && stepValue.horas_por_dia[1] === '00') || stepValue.horas_por_dia === null) {
        errors = { ...errors, horas_por_dia: 'É obrigatório informar uma hora.' }
      }
      if (stepValue.assistencia_onsurance === null) {
        errors = { ...errors, assistencia_onsurance: 'É obrigatório escolher uma opção!' }
      }
      break
    case 3:
      if (stepValue.nome === '') {
        errors = { ...errors, nome: 'É obrigatório informar seu nome!' }
      }
      if (stepValue.sobreNome === '') {
        errors = { ...errors, sobreNome: 'É obrigatório informar seu sobrenome!' }
      }
      if (stepValue.telefone === '') {
        errors = { ...errors, telefone: 'É obrigatório informar o seu número!' }
      } else if (!/\(\d{2}\)\s\d{4,5}-\d{4}/g.test(stepValue.telefone)) {
        errors = { ...errors, telefone: 'Esse número de telefone não é válido!' }
      }
      if (stepValue.cep === '') {
        errors = { ...errors, cep: 'É obrigatório informar seu cep!' }
      }
      if (stepValue.email === '') {
        errors = { ...errors, email: 'É obrigatório informar seu email!' }
      } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(stepValue.email)) {
        errors = { ...errors, email: 'Email inválido, por favor insira um email válido' }
      }
      break
    default:
      console.log('Error no útils, linha 100')
      break
  }
  return errors
}
